import React from "react";
import { NavLink } from "react-router-dom";

function Commen(props) {
  return (
    <>
      <section id="header" className="d-flex align-items-center">
        <div className="container-fluid">
          <div className="row">
            <div className="col-10 mx-auto">
              <div className="row">
                {/* order is use for :- kaya divaice ma su pahela dekhadvu 6  */}
                {/* aahi nichenu div mobile ma 2 nd  dekhase becues order 2 chh and laptop ma first becues order-lg-1 ch */}
                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                  <h1>
                    {props.name}
                    <strong className="brand-name"> {props.name2} </strong>
                  </h1>
                  <h2 className="my-3">
                    we are the team of talanted devlopers
                  </h2>
                  <div className="mt-3">
                    <NavLink to={props.visit} className="btn-get-started">
                      {props.btname}
                    </NavLink>
                  </div>
                </div>

                <div className="col-lg-6 order-1 order-lg-2 header-img">
                  <img
                    src={props.imgsrc}
                    className="img-fluid animated"
                    alt="img not found"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default Commen;
